			                                </div>
		                                </div>
		                                <div class="form-group" style="text-align: end;">
			                                <div>
                                                <button type="reset" class="btn btn-secondary waves-effect m-l-5"> Hy </button>
				                                <button type="submit" class="btn btn-primary waves-effect waves-light"> Lu </button>
			                                </div>
		                                </div>
	                                </form>
                                </div>
                                <p></p>
                            </div>
                            <!-- <div class="modal-footer">
                                        <button type="button" class="btn btn-secondary waves-effect" data-dismiss="modal">Hy</button>
                                        <button type="button" class="btn btn-primary waves-effect waves-light">Xc nhn</button>
