</template>



<script>
import axios from 'axios';
import {
  mapGetters,
    mapActions
} from 'vuex';
const customLabels = {
    first: '<<',
    last: '>>',
    previous: '<',
    next: '>'
};
export default {
    name: 'TestManger',
    computed: {
        //
            ...mapGetters([
               'major',
                'job',
                'jobData',
                'tableData',
                'jobPopup',
                 'jobDataPopup',
                'majorPopup'
            ]),
        },
    mounted() {
    this.getAllMajors();
    this.onMajorSelect();
  },
    methods: {
        getTableData() {
             this.$store.dispatch('onGetQuestionsAction');
           },
           onMajorSelect(major, type) {
             this.$store.dispatch('onMajorSelectAction', {major, type});
           },
           onJobSelect(job, type) {
             this.$store.dispatch('onJobSelectAction', {job, type});
           },
        onChangePage(pageOfItems) {
            this.pageOfItems = pageOfItems;
        },
        getAllMajors() {
        let url = 'https://localhost:44326/api/ChuyenNganh';
        axios.get(url).then((response) => {
        this.majorJson = response.data; 
      });
    },
    addQuestion() {
        let url = 'https://localhost:44326/api/CauHoi';
        axios.get(url).then((response) => {
        this.majorJson = response.data; 
      });
    },
    }, data () {
    return {
            pageOfItems: [],
            customLabels,
            pageSize: 5,
            majorJson: "",
    }
  },
    
}
</script>








